import axios from 'axios'
import JSONBig from 'json-bigint'
import store from '@/store'
import router from '@/router'

const instance = axios.create({
  baseURL: 'http://ttapi.research.itcast.cn/',
  transformResponse: [(data) => {
    try {
      return JSONBig.parse(data)
    } catch (error) {
      return data
    }
  }]
})
// token
instance.interceptors.request.use(config => {
  // tokenVuexstateusertoken
  if (store.state.user.token) {
    config.headers.Authorization = `Bearer ${store.state.user.token}`
  }
  // console.log(config)

  return config
}, err => Promise.reject(err)
)
// 
instance.interceptors.response.use(res => {
  try {
    return res.data.data
  } catch (error) {
    return res
  }
}, async err => {
  if (err.response && err.response.status === 401) {
    const loginConfig = { path: '/login', query: { returnUrl: router.currentRoute.path } }
    const user = store.state.user
    // url
    if (!user || !user.token || !user.refresh_token) {
      router.push(loginConfig)
      return Promise.reject(err)
    }
    try {
      // axiostokenrefresh_token
      const { data: { data } } = await axios({
        method: 'PUT',
        url: 'http://ttapi.research.itcast.cn/app/v1_0/authorizations',
        headers: {
          Authorization: `Bearer${user.refresh_token}`
        }
      })
      // stateuser
      // user
      // mutationssetUser
      store.commit('setUser', {
        token: data.token,
        refresh_token: user.refresh_token
      })
      // err promise(axios) promise
      // instance({})
      //  err.config
      return instance(err.config)
    } catch (error) {
      // token
      store.commit('delUser')
      router.push(loginConfig)
      return Promise.reject(err)
    }
  }
  return Promise.reject(err)
})
// 
// 
// axios(config)
// // Send a POST request
// axios({
//   method: 'post',
//   url: '/user/12345',
//   data: {
//     firstName: 'Fred',
//     lastName: 'Flintstone'
//   }
// })
export default (url, method, data) => {
  return instance({
    url,
    method,
    [method.toLowerCase() === 'get' ? 'params' : 'data']: data
  })
}
